import "./consiglio.js";

$(document).ready(function () {
    var urlParams = new URLSearchParams(window.location.search);
    if(urlParams.has('ref_type_id') && urlParams.has("detail_obj_id")) {
        var url_to_call = apibase.baseurl + urlParams.get("ref_type_id") + "/" + urlParams.get("detail_obj_id");
        $.ajax({url:url_to_call,success:function (result_api) {

                $("#title").text(result_api.title);

                $("#obj_tabs_nav").html(' <div class="nav nav-tabs border-news ml-2 mr-2" id="nav-tab" role="tablist">\n' +
                    '                    <a class="nav-item nav-link active" id="nav-tab1-tab" data-toggle="tab" href="#nav-tab1" role="tab" aria-controls="nav-tab1" aria-selected="false">STRUTTURA</a>\n' +
                    '                    <a class="nav-item nav-link " id="nav-tab2-tab" data-toggle="tab" href="#nav-tab2" role="tab" aria-controls="nav-tab2" aria-selected="true">SERVIZI</a>\n' +
                    '                    <a class="nav-item nav-link" id="nav-tab3-tab" data-toggle="tab" href="#nav-tab3" role="tab" aria-controls="nav-tab3" aria-selected="false">ATTIVITÀ</a>\n' +
                    '\n' +
                    '                </div>');

                $("#obj_anagrafica").html('<h6 class="dark-obj-name">Contatti</h6><div class="col">\n' +
                    '                        <div class="row mt-2" id="address_ufficio_area">\n' +
                    '                        <div class="col col-sm-1">\n' +
                    '                            <div class="avatar size-lg text-dark" id="mail_avatar">\n' +
                    '                                <svg class="icon icon-secondary"><use xlink:href="svg/sprite.svg#it-pa"></use></svg>\n' +
                    '                                <span class="sr-only">Mail</span>\n' +
                    '                            </div>\n' +
                    '                        </div>\n' +
                    '                            <div class="col pr-1"><span class="it-griditem-text" id="adress_ufficio"></span></div>\n' +
                    '                        </div>\n' +
                    '                        <div class="row mt-2" id="phone_area">\n' +
                    '                        <div class="col col-sm-1 align-content-center">\n' +
                    '                            <div class="avatar size-lg text-dark">\n' +
                    '                                <svg class="icon icon-secondary"><use xlink:href="svg/sprite.svg#it-mail"></use></svg>\n' +
                    '                                <span class="sr-only">address</span>\n' +
                    '                            </div>\n' +
                    '                        </div>\n' +
                    '                            <div class="col  pr-1"><span class="it-griditem-text" id="email_ufficio"></span></div>\n' +
                    '                        </div>\n' +
                    '                        </div><hr class="separator bordered mb-0" />');



                if(result_api.properties.indirizzo != undefined) {
                    $("#adress_ufficio").text(result_api.properties.indirizzo);
                }else {
                    $("#address_ufficio_area").hide();
                }
                if(result_api.properties.email != undefined) {
                    $("#email_ufficio").text(result_api.properties.email);
                }else {
                    $("#phone_area").hide();
                }


                if(result_api.components.dirigente != undefined) {
                    let dirigente = result_api.components.dirigente;
                    if(dirigente.components.length>0) {
                        $("#obj_anagrafica").append('<h6 class="dark-obj-name">'+result_api.components.dirigente.components[0].properties.tipologia_incarico + ': '+dirigente.components[0].properties.nome+' '+dirigente.components[0].properties.cognome.toUpperCase() +'</h6><div class="col col-sm-12">\n' +
                            '                        <div class="row mt-2" id="mail_area">\n' +
                            '                        <div class="col col-sm-1">\n' +
                            '                            <div class="avatar size-lg text-dark" id="mail_avatar">\n' +
                            '                                <svg class="icon icon-secondary"><use xlink:href="svg/sprite.svg#it-mail"></use></svg>\n' +
                            '                                <span class="sr-only">Mail</span>\n' +
                            '                            </div>\n' +
                            '                        </div>\n' +
                            '                            <div class="col pr-1"><span class="it-griditem-text" id="email_dirigente"></span></div>\n' +
                            '                        </div>\n' +
                            '                        <div class="row mt-2" id="dir_phone_area">\n' +
                            '                        <div class="col col-sm-1">\n' +
                            '                            <div class="avatar size-lg text-dark">\n' +
                            '                                <svg class="icon icon-secondary"><use xlink:href="svg/sprite.svg#it-telephone"></use></svg>\n' +
                            '                                <span class="sr-only">address</span>\n' +
                            '                                                </div>\n' +
                            '                        </div>\n' +
                            '                            <div class="col pr-1"><span class="it-griditem-text" id="phone_dirigente"></span></div>\n' +
                            '                        </div>\n' +
                            '                        <div class="row mt-2" id="fax_area">\n' +
                            '                        <div class="col col-sm-1">\n' +
                            '                            <div class="avatar size-lg text-dark">\n' +
                            '                                <svg class="icon icon-secondary"><use xlink:href="svg/sprite.svg#it-note"></use></svg>\n' +
                            '                                <span class="sr-only">address</span>\n' +
                            '                            </div>\n' +
                            '                        </div>\n' +
                            '                            <div class="col pr-1"><span class="it-griditem-text" id="fax_dirigente"></span></div>\n' +
                            '                        </div>\n' +
                            '                        </div>' +
                            '                        </div>' +
                            '<hr class="separator bordered mb-0" />');
                        if(dirigente.components[0].properties.telefono != undefined) {
                            $("#phone_dirigente").text(dirigente.components[0].properties.telefono);
                        }else {
                            $("#dir_phone_area").hide();
                        }
                        if(dirigente.components[0].properties.email != undefined) {
                            $("#email_dirigente").text(dirigente.components[0].properties.email);
                        }else{
                            $("#mail_area").hide();
                        }
                        if(dirigente.components[0].properties.fax != undefined) {
                            $("#fax_dirigente").text(dirigente.components[0].properties.fax);
                        }else {
                            $("#fax_area").hide();
                        }
                    }
                }
                if(result_api.components.personale_amministrativo != undefined) {
                    let personale_amministrativo = result_api.components.personale_amministrativo;
                    var html_struttura = '<div class="table-responsive d-none d-lg-block"><h5 class="dark-obj-name">'+result_api.components.personale_amministrativo.label+'</h5> </div>';
                    var html_struttura_responsive = '<div class="d-lg-none"><h5 class="dark-obj-name">'+result_api.components.personale_amministrativo.label+'</h5> ';

                    let struttura = personale_amministrativo.components.filter(function (va) {
                        return va.properties.ufficio_stampa === "F";
                    });

                    let ufficio_stampa = personale_amministrativo.components.filter(function (va) {
                        return va.properties.ufficio_stampa === "T";
                    });

                    if(ufficio_stampa.length > 0 ) {
                        var html_ufficio_stampa = '<div class=""><h5 class="dark-obj-name d-none d-lg-block">'+"Ufficio Stampa"+'</h5> </div>';
                        var html_ufficio_stampa_responsive = '<div class="d-lg-none"><h5 class="dark-obj-name">'+"Ufficio Stampa"+'</h5> ';

                        html_ufficio_stampa += '<div class="table-responsive d-none d-lg-block"><table class="table table-hover">'+'<thead class="thead_dark">\n' +
                            '    <tr>\n' +
                            '      <th scope="col">Cognome e Nome</th>\n' +
                            '      <th scope="col">Qualifica</th>\n' +
                            '      <th scope="col">Recapiti</th>\n' +
                            '    </tr>\n' +
                            '  </thead><tbody>';


                        for(var i = 0; i<ufficio_stampa.length;i++ ) {
                            let element = ufficio_stampa[i];
                            let recapiti = "";
                            if(element.properties.email != undefined) {
                                recapiti = element.properties.email + ' <br/>';
                            }
                            if(element.properties.telefono != undefined) {
                                recapiti += 'Tel: '+  element.properties.telefono + '<br/>'
                            }

                            if(element.properties.fax != undefined) {
                                recapiti += 'Fax: '+  element.properties.fax + '<br/>';
                            }
                            html_ufficio_stampa += '<tr><td>'+element.properties.cognome + ' '+element.properties.nome+'</td>';
                            html_ufficio_stampa += '<td>'+element.properties.tipologia_incarico +'</td>';
                            html_ufficio_stampa += '<td>'+recapiti+'</td></tr>';
                            html_ufficio_stampa_responsive += '<div class="bg-title-area row mr-5 ml-2 mb-2 mt-2">'+'<div class="col-12 dark-obj-name"><strong>'+element.properties.cognome + ' ' +element.properties.nome+'</strong></div>'+'<div class="col-12">'+element.properties.tipologia_incarico+'</div>'+'<div class="col-12">'+recapiti+'</div>'+'</div>';
                        }
                        html_ufficio_stampa_responsive += "</div>";
                        html_ufficio_stampa += '</table></div>';

                        $("#nav-tab1").append(html_ufficio_stampa);
                        $("#nav-tab1").append(html_ufficio_stampa_responsive);

                    }

                    if(struttura.length > 0 ) {
                        html_struttura += '<div class="table-responsive d-none d-lg-block"><table class="table table-hover">'+'<thead class="thead_dark">\n' +
                            '    <tr>\n' +
                            '      <th scope="col">Cognome e Nome</th>\n' +
                            '      <th scope="col">Qualifica</th>\n' +
                            '      <th scope="col">Recapiti</th>\n' +
                            '    </tr>\n' +
                            '  </thead><tbody>';

                        let contatti = "";


                        for(var i = 0; i<struttura.length;i++ ) {
                            let element = struttura[i];
                            if(element.properties.email != undefined) {
                                contatti = element.properties.email + "</br>";
                            }
                            if(element.properties.telefono != undefined ) {
                                contatti += 'Tel: '+ element.properties.telefono + '<br/>';
                            }
                            if(element.properties.fax != undefined) {
                                contatti += 'Fax: '+  element.properties.fax + '<br/>';
                            }
                            html_struttura += '<tr><td>'+element.properties.cognome + ' '+element.properties.nome+'</td>';
                            html_struttura += '<td>'+element.properties.tipologia_incarico +'</td>';
                            html_struttura += '<td>'+contatti+'</td></tr>';
                            html_struttura_responsive += '<div class="bg-title-area row mr-5 ml-2 mb-2 mt-2">'+'<div class="col-12 dark-obj-name"><strong>'+element.properties.cognome + ' ' +element.properties.nome+'</strong></div>'+'<div class="col-12">'+element.properties.tipologia_incarico+'</div>'+'<div class="col-12">'+contatti+'</div>'+'</div>';

                        }
                        html_struttura_responsive += '</div>';
                        html_struttura += '</table></div>';

                        $("#nav-tab1").append(html_struttura);
                        $("#nav-tab1").append(html_struttura_responsive);
                    }
                }
                if(result_api.components.servizio_ufficio != undefined) {
                    $("#nav-tab2").html('<div ><h5 class="dark-obj-name">'+"I Servizi dell'Ufficio"+'</h5> </div>'+'<div id="accordionDiv1"  role="tablist"></div>');

                    if (result_api.components.servizio_ufficio.components.length > 0 ){

                        let servizi_ufficio = result_api.components.servizio_ufficio.components;
                        servizi_ufficio.forEach(function (element) {
                            $("#accordionDiv1").append('<div class="collapse-header  border-bottom-bg" id="heading'+element.id+'">\n' +
                                '                        <button  class="dark-obj-name" data-toggle="collapse" data-target="#accordion'+element.id+'" aria-expanded="false" aria-controls="accordion"'+element.id+' id="btn'+element.id+'">\n' +element.title+
                                '                        </button>\n' +
                                '                    </div><div id="accordion'+element.id+'" class="collapse" role="tabpanel" aria-labelledby="headingPrevisione" data-parent="#accordionDiv1">\n' +
                                '                        <div class="collapse-body" id="accordion'+element.id+'Body">\n' +
                                '                        </div>\n' +
                                '                    </div>');

                            $("#accordion"+element.id+"Body").html(element.properties.descrizione);
                        })
                    }else {
                        $("#accordionDiv1").html("Nessun servizio per questo ufficio");
                    }
                }

                if(result_api.components.attivita_ufficio != undefined) {
                    $("#nav-tab3").html('<div ><h5 class="dark-obj-name">'+"Le Attività dell'Ufficio"+'</h5> </div>'+'<div id="accordionDiv2"  role="tablist"></div>');
                    if (result_api.components.attivita_ufficio.components.length > 0 ){
                        let servizi_ufficio = result_api.components.attivita_ufficio.components;
                        servizi_ufficio.forEach(function (element) {
                            $("#accordionDiv2").append('<div class="collapse-header  border-bottom-bg" id="heading'+element.id+'">\n' +
                                '                        <button  class="dark-obj-name" data-toggle="collapse" data-target="#accordion'+element.id+'" aria-expanded="false" aria-controls="accordion"'+element.id+' id="btn'+element.id+'">\n' +element.title+
                                '                        </button>\n' +
                                '                    </div><div id="accordion'+element.id+'" class="collapse" role="tabpanel" aria-labelledby="headingPrevisione" data-parent="#accordionDiv1">\n' +
                                '                        <div class="collapse-body" id="accordion'+element.id+'Body">\n' +
                                '                        </div>\n' +
                                '                    </div>');
                            var descrizione_attivita = "";
                            if(element.properties.descrizione != undefined) {
                                descrizione_attivita = element.properties.descrizione +"</br>";
                            }

                            $("#accordion"+element.id+"Body").html(descrizione_attivita + element.properties.fonte_legislativa);
                        })
                    }
                }


            },error:function (res_err) {
                var label = "";
                var sectionId = urlParams.get('section_id')+"label";
                var retrievedObject = localStorage.getItem(sectionId);
                if(retrievedObject != undefined) {
                    label = retrievedObject;
                }else   {
                    label = sectionId;
                }
                $("#title").text(label);
                $("#container_base").hide();
                $("#side_menu").attr('class', 'col-12 col-sm-12 col-md-12 link-list-wrapper');

            }});

    }
})